import React, {Component} from 'react';
import { Header, Footer, Loader, Space, ComponentLeader } from '../widgets';
import { Link } from 'react-router';
import moment from 'moment';
import Particles from 'react-particles-js';

class Pricing extends Component{
    renderContactUs(){
        return (
            <div className="take_actions">
            <div className="card">
                <div className="title">
                    Explore in the Dashboard
                </div>
                <Link to="dashboard/start">
                    <div className="action_button">
                        Login/SignUp Now
                    </div>
                </Link>
                <div className="description">
                    It is free to start!
                    <br/>
                    Start your <span className="underline">succesful college learning path</span>
                </div>
            </div>
            </div>
        )
    }
    render() {
        return(
            <div>
                <Header className="fixed"/>
                <ComponentLeader title="Pricing" />
                <div className="pricing-component">
                    <img className="hidden-xs-down" src="https://s3-us-west-1.amazonaws.com/yeah-assets/medias/home/pricing.png"/>
                    <img className="hidden-sm-up" src="https://s3-us-west-1.amazonaws.com/yeah-assets/medias/home/pricing-mobile.png"/>
                </div>
                {this.renderContactUs()}
                <Footer />
            </div>
        )
    }
}

export default Pricing;


