import React, {Component} from 'react';
import commentData from './data';

export default class SProgram extends Component {
    componentWillMount(){
        // console.log('commentData: ', commentData)
    }
    renderCard(){
        return commentData.map(student=>{
            return (
                <div className="card comment" key={student.name}>
                <div className="university">{student.university}</div>
                <div className="body">
                    <div className="photo" style={{backgroundImage: `url(${student.photo})` }}></div>
                    <div className="name">{student.name}</div>
                    <div className="major">{student.major}&nbsp;Major</div>
                    <hr />
                    <div className="content">...{student.content}...</div>
                    <hr/>
                    <div className="service">2 Year Program (2015-2016)</div>
                    <div className="acceptBy">Got other admissions: &nbsp;{student.acceptBy}</div>
                </div>
                </div>
            )
        })
    }
  render() {
    return (
      <div className="section comment" style={{background: "#f4f4ef"}}>
      <div className="title">Successful Stories</div>
        <div className="h-scrollable no-border">
            {this.renderCard()}
        </div>
      </div>
    );
  }
}


